/* Feature PROJECT STARTS */
.feature-project-container{
  /* border: 2px solid purple; */
  width: 100%;
  height: auto;
  position: relative;
}

/* FEATURE IMG START */
.feature-img-container {
  overflow: auto;
}

.feature-project-container img{
  width: 100%;
  height: auto;
  object-fit: cover;
  }

/* FEATURE DESCRIPTION START */
.feature-description-container{
  width: 100%;
  display: flex;
  justify-content: center;
  text-align: center;
  overflow: auto
}

.description-card-style {
  /* border: 1px solid purple; */
  width: 100%;
  height: auto;
  margin: 0.9rem;
  box-shadow: rgb(0 0 0 / 20%) 0px 4px 8px 0px;
  background-color: var(--palette-threeb);
  overflow: auto;
}

.feature-project-description {
  display: inline-block;
  padding: 5vw 5vw 0.5vw 5vw;
  /* border: solid 3px  purple; */
}

.feature-project-description h3 {
  font-size: 7vw;
  color: black;
}
.feature-project-description p {
 font-family: 'Avenir', 'Helvetica', 'Clear Sans', 'Sans-serif';
}

.feature-buttons {
  width: 100%;
  display: flex;
  margin: 0 0 1rem 0;
  /* border: solid 2px  blue; */
  padding: 0 5vw 0 5vw;
  position: relative;
  justify-content: center;
  /* justify-content: space-between; */
}

.feature-buttons a {
  width: 100%;
  max-width: 200px;
  border: solid 2px  var(--palette-five);
  margin: 0 2.6rem 0 0rem;
  padding: 0.6rem 3rem;
  text-align: center;
  font-size: 0.8em;
  color: var(--small-font-color);
  transition: 0.4s;
}

.feature-buttons a:hover {
  background-color: var(--palette-five);
  color: white;
  transition: 0.3s;
}
/* FEATURE PROJECT ENDS */


  /* SUMMARY STARTS */
  .myProjectSummary {
    background-color: var(--pallett-eight);
    /* background-color: white; */
    margin: 0;
    padding: 2vw 21.3vw;
    text-align: center;
  }

  .myProjectSummary h1 {
    font-size: 2.5rem;
    color: black;
  }
   /* SUMMARY STARTS */

  /* PROJECT CARDS START */
 
 .card-container {
   display: flex;
   /* flex-wrap: wrap; */
   /* This is a mobile style that will be overridden in media queries */
   flex-direction: column;
   align-items: center;
   justify-content: center;
   background-color: var(--pallett-eight);
   position: relative;
   /* border: 2px solid aqua; */

 }
 
 .project-card {
   display: block;
   position: relative;
   max-width: 500px;
  
   margin: 0.5rem;
   text-align: center;
   background-color: var(--pallett-eight);
   /* box-shadow: 0px 0px 3px rgba(0, 0, 0, 0.2); */
   /* border: 3px solid green; */
   overflow: auto;
   font-size: 1rem;
 }
 
 .project-card img {
   object-fit: cover;
   display: block;
   width: 100%;
   height: 100%;
   position: relative;
   /* border: 5px solid purple; */
 }

 .project-card span {
   font-family: 'Avenir', 'Helvetica', 'Clear Sans', 'Sans-serif';
 }
.imageContainer {
   position: relative;
   overflow: auto;
   width: 100%;
   height: 70%;
   /* border: 2px solid yellow; */

 }

 .project-card:hover .overlay {
  height: 12%;
  padding: 0.5rem 0 2rem 0
}
 
 .overlay {
   bottom: 0;
   position: absolute;
   background-color: var(--pallett-nine);
   display: block;
   width: 100%;
   height: 0;
   /* height: 10%; */
   font-size: 1rem;
   transition: 0.5s ease;
   /* border: 2px solid blue; */
 }

 .overlay a {
   color: var(--small-font-color);
   font-family: 'Avenir', 'Helvetica', 'Clear Sans', 'Sans-serif';
 }

 .descriptionContainer {
   overflow: auto;
   /* border: 3px solid red; */
   width: 100%;
   height: auto;
   padding: 1rem;
   position: relative;
 }

 .descriptionContainer a {
   text-decoration: none;
   color: var(--small-font-color)
 }

 .descriptionContainer h3 {
  font-size: 1.50rem;
}

.descriptionContainer button {
  background-color: var(--pallett-eight);
}
 /* PROJECT CARDS END */

 /* MODAL STARTS */
 .modal-style {
  border: 2px solid purple;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  background-color: white;
  padding: 7vw;
  z-index: 1000;
  transition: 1s;

  text-align: center;
 }
 
 .modal-style h1 {
   color: black;
   margin: 0 0 2rem 0;

 }

 .modal-style h4 {
   margin: 1rem;
 }

 .modal-style p {
  font-family: 'Avenir', 'Helvetica', 'Clear Sans', 'Sans-serif';
 }

 .modal-style section {
   margin-top: 2rem;
 }
 .modal-style button {
   font-size: 1rem; 
 }

 .modal-style a {
  color: var(--small-font-color)
 }

 

 .modal-overlay {
   position: fixed;
   left: 0;
   top: 0;
   right: 0;
   bottom: 0;
   background-color: rgba(0, 0, 0, 0.7);
   z-index: 1000;
   transition: 1s;
 }

 /* MODAL ENDS */

   /* RESPONSIVENESS STARTS ~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~*/
 
