}

/* NECT RESPONSIVE WINDOW SIZE ~~~~~~ */
  
  @media screen and (min-width: 1024px) {
    .project-card {
      width: calc(33.33% - 1rem);
      max-height: 450px;
      overflow: scroll;
    }
    
  }